import React, { useEffect, useState } from "react"
import axios from "axios"
import _ from "lodash"
import {
  Col,
  Container,
  Row,
  Form,
  ListGroup,
  Table,
  Button,
} from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"
import {
  addStudentToSeriesLevel,
  getSeriesDetails,
  getSeriesLevelDetails,
} from "../actions/seriesAction"
import { listStudents } from "../actions/studentsAction"
import PORT from "../utils/baseURL"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { SERIES_LEVEL_ADD_STUDENT_RESET } from "../constants/seriesConstants"

const LevelStudentAddScreen = () => {
  const [selectedStudents, setSelectedStudents] = useState([])
  const [students, setStudents] = useState([])
  const [formattedStudents, setFormattedStudents] = useState([])
  const [unExistStudents, setUnExistStudents] = useState([])

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { series } = seriesDetails

  const seriesLevelDetails = useSelector(state => state.seriesLevelDetails)
  const { level } = seriesLevelDetails

  //   const studentList = useSelector(state => state.studentList)
  //   const {
  //     loading: studentsLoading,
  //     error: studentsError,
  //     students,
  //   } = studentList

  const { seriesId, levelId } = useParams()

  const dispatch = useDispatch()

  const navigate = useNavigate()

  useEffect(() => {
    const fetchStudents = async () => {
      try {
        const { data } = await axios.get(`${PORT}/api/students/allStudents`)

        setStudents(data)
      } catch (error) {
        console.log(error)
      }
    }
    fetchStudents()
  }, [])

  useEffect(() => {
    dispatch(listStudents())
    dispatch(getSeriesDetails(seriesId))
    dispatch(getSeriesLevelDetails(seriesId, levelId))
  }, [dispatch, seriesId, levelId])

  //  for checking the students existence in the level students
  useEffect(() => {
    if (students && students.length > 0 && level && level.levelStudents) {
      const notInLevelStudents = _.differenceBy(
        students,
        level.levelStudents,
        "_id"
      )

      setUnExistStudents(notInLevelStudents)
    }
  }, [level, students])

  const handleStudentChecked = (e, student) => {
    if (e.target.checked) {
      const existStudent = selectedStudents.find(
        stu => stu.id.toString() === student.id.toString()
      )
      if (!existStudent) {
        setSelectedStudents(prevStudents => [...prevStudents, student])
      } else {
        return
      }
    } else if (!e.target.checked) {
      const existStudent = selectedStudents.find(
        stu => stu.id.toString() === student.id.toString()
      )
      if (existStudent) {
        const filteredSelectedStudents = selectedStudents.filter(
          stu => stu.id.toString() !== student.id.toString()
        )

        setSelectedStudents(filteredSelectedStudents)
      } else {
        return
      }
    }
  }

  const seriesLevelStudentAdd = useSelector(
    state => state.seriesLevelStudentAdd
  )
  const {
    success: studentAddSuccess,
    loading: studentAddLoading,
    error: studentAddError,
  } = seriesLevelStudentAdd

  useEffect(() => {
    if (studentAddSuccess) {
      dispatch({ type: SERIES_LEVEL_ADD_STUDENT_RESET })
      navigate(`/serieses/${seriesId}/levels/${levelId}/students`)
    }
  }, [
    dispatch,
    navigate,
    studentAddSuccess,
    selectedStudents,
    seriesId,
    levelId,
  ])

  const handleAddStudents = async e => {
    e.preventDefault()

    selectedStudents.forEach(stu => {
      return setFormattedStudents(prevStudents => [...prevStudents, stu.id])
    })

    if (
      formattedStudents &&
      formattedStudents.length === selectedStudents.length
    ) {
      dispatch(
        addStudentToSeriesLevel(seriesId, levelId, {
          levelStudents: formattedStudents,
        })
      )
    }
  }

  return studentAddLoading ? (
    <Loader />
  ) : studentAddError ? (
    <Message variant="danger">{studentAddError}</Message>
  ) : (
    series &&
    level && (
      <Container>
        <h4>
          Add Student to {series.name} series in {level.levelName}
        </h4>

        <Row>
          <Col md={6}>
            <div
              className="mt-4"
              style={{ overflowY: "scroll", height: "70vh" }}
            >
              <Form.Label as="legend">Select Students to add</Form.Label>
              <Table size="sm" borderless striped responsive>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>F/Name</th>
                    <th>Select</th>
                  </tr>
                </thead>
                <tbody>
                  {unExistStudents &&
                    unExistStudents.length > 0 &&
                    unExistStudents.map(student => (
                      <tr key={student._id}>
                        <td>{student.name}</td>
                        <td>{student.fatherName}</td>
                        <td>
                          <Form.Group>
                            <Form.Check
                              type="checkbox"
                              onChange={e =>
                                handleStudentChecked(e, {
                                  id: student._id,
                                  name: student.name,
                                  fatherName: student.fatherName,
                                })
                              }
                            ></Form.Check>
                          </Form.Group>
                        </td>
                      </tr>
                    ))}
                </tbody>
              </Table>
            </div>
          </Col>
          <Col md={6}>
            <ListGroup
              className="mt-4"
              style={{ overflowY: "scroll", height: "65vh" }}
            >
              <ListGroup.Item>
                <strong>Selected Students</strong>
              </ListGroup.Item>
              <Table size="sm" hover bordered>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>F/Name</th>
                  </tr>
                </thead>
                <tbody>
                  {selectedStudents.length > 0 &&
                    selectedStudents.map(student => (
                      <tr key={student.id}>
                        <td>{student.name}</td>
                        <td>{student.fatherName}</td>
                      </tr>
                    ))}
                </tbody>
              </Table>
            </ListGroup>
            <Form onSubmit={handleAddStudents}>
              <Button type="submit" variant="outline-success" size="sm">
                Add Students
              </Button>
            </Form>
          </Col>
        </Row>
      </Container>
    )
  )
}

export default LevelStudentAddScreen
