import React, { useEffect, useState } from "react"
import { Button, Container, Form } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"

import { adminGetUserProfile, adminUpdateUser } from "../actions/usersAction"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { USER_UPDATE_RESET } from "../constants/usersConstants"

const UserLoginScreen = () => {
  const { userId } = useParams()

  const [isAdmin, setIsAdmin] = useState(false)

  const adminUserDetails = useSelector(state => state.adminUserDetails)
  const {
    user: userDetails,
    loading: userLoading,
    error: userError,
  } = adminUserDetails

  const adminUserUpdate = useSelector(state => state.adminUserUpdate)
  const { success, loading, error } = adminUserUpdate

  const navigate = useNavigate()

  const dispatch = useDispatch()

  useEffect(() => {
    if (!userDetails) {
      dispatch(adminGetUserProfile(userId))
    } else {
      if (userDetails.name) {
        setIsAdmin(userDetails.isAdmin)
      }
    }

    if (success) {
      dispatch({ type: USER_UPDATE_RESET })
      navigate("/users")
    }
  }, [dispatch, userId, navigate, success, userDetails])

  const handleSubmit = e => {
    e.preventDefault()
    dispatch(adminUpdateUser(userId, { isAdmin }))
  }

  return (
    <>
      <h2 className="text-center">User Edit</h2>
      {loading ? (
        <Loader />
      ) : userLoading ? (
        <Loader />
      ) : (
        <Container
          className="justify-content-center p-5"
          style={{ width: "50%" }}
        >
          <Form onSubmit={handleSubmit}>
            <Form.Group controlId="isAdmin" className="mt-4 mx-2 ">
              <Form.Label as="legend">Make the user as an admin</Form.Label>
              <Form.Check
                type="checkbox"
                label="Mark As Admin"
                checked={isAdmin}
                value={isAdmin}
                onChange={e => setIsAdmin(e.target.checked)}
              />
            </Form.Group>
            {error && <Message variant="danger">{error}</Message>}
            {userError && <Message variant="danger">{userError}</Message>}
            <Button type="submit" variant="outline-warning" className="mt-3">
              Update
            </Button>
          </Form>
        </Container>
      )}
    </>
  )
}

export default UserLoginScreen
