import React from "react"
import { Table } from "react-bootstrap"

import Loader from "./Loader"
import Message from "./Message"

const ReportsList = ({ studentsData, timePeriod }) => {
  const { loading, error, students, from } = studentsData
  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <div style={{ width: "50%" }}>
          <strong>From : </strong>
          <p style={{ display: "inline-block" }}>{from}</p>
        </div>
        <div style={{ width: "50%" }}>
          {studentsData && studentsData.to && (
            <th>
              <strong>To : </strong>
              <p style={{ display: "inline-block" }}>{studentsData.to}</p>
            </th>
          )}
        </div>
      </div>
      <Table
        striped
        bordered
        hover
        responsive
        size="sm"
        className="print-container"
      >
        <thead>
          <tr>
            <th>Id</th>
            <th>Name</th>
            <th>F/Name</th>
            <th>Gender</th>
            <th>Phone Number</th>
            <th>Registered At</th>
            <th>Registration Fees</th>
            <th>Card Fees</th>
          </tr>
        </thead>

        <tbody>
          {students &&
            students.length > 0 &&
            students.map(student => (
              <tr key={student._id}>
                <td>{student._id.substring(16, 25)}</td>
                <td>{student.name}</td>
                <td>{student.fatherName}</td>
                <td>{student.gender}</td>
                <td>{student.phoneNumber}</td>
                <td>{student.createdAt.substring(0, 10)}</td>
                <td>
                  {student.registrationFees.feesAmount}

                  <div className="d-inline-block mx-2 ">
                    {student.registrationFees.isPaid ? (
                      <i className="fa fa-check" style={{ color: "green" }}></i>
                    ) : (
                      <i className="fa fa-times" style={{ color: "red" }}></i>
                    )}
                  </div>
                  <div className="d-inline-block mx-2">
                    {student.registrationFees.isPaid &&
                      student.registrationFees.paidAt.substring(0, 10)}
                  </div>
                </td>
                <td>
                  {student.cardFees.feesAmount}

                  <div className="d-inline-block mx-2 ">
                    {student.cardFees.isPaid ? (
                      <i className="fa fa-check" style={{ color: "green" }}></i>
                    ) : (
                      <i className="fa fa-times" style={{ color: "red" }}></i>
                    )}
                  </div>
                  <div className="d-inline-block mx-2">
                    {student.cardFees.isPaid &&
                      student.cardFees.paidAt.substring(0, 10)}
                  </div>
                </td>
              </tr>
            ))}
        </tbody>
      </Table>
    </>
  )
}

export default ReportsList
