import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Form, Button, Row, Col, Container } from "react-bootstrap"
import { Link, useNavigate, useParams } from "react-router-dom"

import FormContainer from "../components/FormContainer"
import { updateStudent, listStudent } from "../actions/studentsAction"
import Message from "../components/Message"
import Loader from "../components/Loader"
import { UPDATE_STUDENT_RESET } from "../constants/studentConstants"

const StudentEditScreen = () => {
  const [name, setName] = useState("")
  const [fatherName, setFatherName] = useState("")
  const [gender, setGender] = useState("")
  const [studentPhone, setStudentPhone] = useState("")
  const [fatherPhone, setFatherPhone] = useState("")
  const [address, setAddress] = useState("")
  const [fees, setFees] = useState(0)
  const [isPaid, setIsPaid] = useState(false)

  const dispatch = useDispatch()

  const navigate = useNavigate()

  const { studentId } = useParams()

  const studentDetails = useSelector(state => state.studentDetails)
  const {
    loading: detailsLoading,
    error: detailsError,
    student,
  } = studentDetails

  const studentUpdate = useSelector(state => state.studentUpdate)
  const { loading, error, success } = studentUpdate

  useEffect(() => {
    if (!student.name) {
      dispatch(listStudent(studentId))
    } else {
      if (student.name) {
        setName(student.name)
        setFatherName(student.fatherName)
        setGender(student.gender)
        setStudentPhone(student.studentPhone)
        setFatherPhone(student.fatherPhone)
        setAddress(student.address)
        setFees(student.fees)
        setIsPaid(student.isPaid)
      }
    }
    if (success) {
      dispatch({ type: UPDATE_STUDENT_RESET })
      navigate("/students")
    }
  }, [dispatch, navigate, studentId, student, success])

  const handleSubmit = e => {
    e.preventDefault()

    dispatch(
      updateStudent(studentId, {
        name,
        fatherName,
        gender,
        studentPhone,
        fatherPhone,
        fees,
        address,
        isPaid,
      })
    )
  }

  return detailsLoading ? (
    <Loader />
  ) : detailsError ? (
    <Message variant="danger">{detailsError}</Message>
  ) : loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <Container fluid className="mt-5">
      <FormContainer>
        <Row>
          <Col md={5}>
            <Link to="/students" className="btn btn-sm btn-outline-secondary">
              Go Back
            </Link>
          </Col>
          <Col></Col>
        </Row>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md={6}>
              <Form.Group controlId="name" className="m-2">
                <Form.Label> Name </Form.Label>
                <Form.Control
                  type="text"
                  value={name}
                  placeholder="Enter Name"
                  onChange={e => setName(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group controlId="fatherName" className="m-2">
                <Form.Label> Father Name </Form.Label>
                <Form.Control
                  type="text"
                  value={fatherName}
                  placeholder="Enter Name"
                  onChange={e => setFatherName(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <Form.Group className="m-2">
                <Form.Label htmlFor="gender">Select Gender</Form.Label>
                <Form.Select
                  id="gender"
                  value={gender}
                  onChange={e => setGender(e.target.value)}
                >
                  <option value="male">Male</option>
                  <option value="female">Female</option>
                </Form.Select>
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group controlId="fees" style={{ width: "50%" }}>
                <Form.Label>Fees</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Student Fees"
                  value={fees}
                  onChange={e => setFees(e.target.value)}
                ></Form.Control>
              </Form.Group>
              <Form.Group controlId="isPaid" className="mt-4 mx-2 ">
                <Form.Check
                  type="checkbox"
                  label="Is Paid"
                  checked={isPaid}
                  value={isPaid}
                  onChange={e => setIsPaid(e.target.checked)}
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <Form.Group controlId="fatherPhone" className="m-2">
                <Form.Label> Student Father Phone Number </Form.Label>
                <Form.Control
                  type="text"
                  value={fatherPhone}
                  placeholder="Enter Student Father Phone Number"
                  onChange={e => setFatherPhone(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group controlId="address" className="m-2">
                <Form.Label> Address </Form.Label>
                <Form.Control
                  type="text"
                  value={address}
                  placeholder="Enter Address"
                  onChange={e => setAddress(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <Form.Group controlId="studentPhone" className="m-2">
                <Form.Label> Student Phone Number </Form.Label>
                <Form.Control
                  type="text"
                  value={studentPhone}
                  placeholder="Enter Student Phone Number"
                  onChange={e => setStudentPhone(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
          </Row>
          <Button type="submit" variant="primary" className="my-4">
            Update Student
          </Button>
        </Form>
      </FormContainer>
    </Container>
  )
}

export default StudentEditScreen
