import React from "react"
import { Container, ListGroup } from "react-bootstrap"

const HomeScreen = () => {
  return (
    <Container>
      <h1>(Kamil Institute)</h1>

      <hr />
      <h4>How the program work ?</h4>
      <ListGroup variant="flush">
        <ListGroup.Item>
          <strong>Want to add new Student : </strong> in the toolbar click the
          (Add Student Button)
        </ListGroup.Item>
        <ListGroup.Item>
          <strong>Want to see all the Students in the Database : </strong> in
          the toolbar click the (Students Button)
        </ListGroup.Item>
        <ListGroup.Item>
          <strong>Want to see the overall overview of the data : </strong>
          in the toolbar click the (Overview Button)
        </ListGroup.Item>
      </ListGroup>
      <hr />
      <p>
        <strong>The Teachers pages are same as the Students</strong>
      </p>
      <footer style={{ marginTop: 200 }}>
        <p> @Copyrights Atlas LLC </p>
      </footer>
    </Container>
  )
}

export default HomeScreen
