import React, { useEffect } from "react"
import { Button, Col, Container, ListGroup, Row, Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { Link, useParams, useNavigate } from "react-router-dom"

import Loader from "../components/Loader"
import Message from "../components/Message"
import { getSeriesDetails, deleteSeriesById } from "../actions/seriesAction"
import { DELETE_SERIES_BY_ID_RESET } from "../constants/seriesConstants"

const SeriesInfoScreen = () => {
  const { seriesId } = useParams()

  const dispatch = useDispatch()

  const navigate = useNavigate()

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { loading, error, series } = seriesDetails

  const seriesDeleteById = useSelector(state => state.seriesDeleteById)
  const {
    loading: deleteSeriesLoading,
    error: deleteSeriesError,
    success,
  } = seriesDeleteById

  useEffect(() => {
    dispatch(getSeriesDetails(seriesId))

    if (success) {
      navigate("/serieses")
      dispatch({ type: DELETE_SERIES_BY_ID_RESET })
    }
  }, [dispatch, navigate, seriesId, success])

  const handleSeriesDelete = () => {
    if (window.confirm("Are You Sure ?")) {
      dispatch(deleteSeriesById(seriesId))
    }
  }
  const handleSeriesUpdate = () => {
    navigate(`/serieses/${seriesId}/edit`)
  }

  return loading ? (
    <Loader />
  ) : deleteSeriesLoading ? (
    <Loader />
  ) : deleteSeriesError ? (
    <Message variant="danger">{deleteSeriesError}</Message>
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    series && (
      <Container fluid className="px-5 py-2">
        <h3 className="text-center mb-4">Series Info</h3>
        <Row>
          <Col md={4}>
            <Row className="mb-3">
              <Col>
                <Button
                  variant="outline-danger"
                  type="button"
                  size="sm"
                  onClick={handleSeriesDelete}
                >
                  Delete Series
                </Button>
              </Col>
              <Col>
                <Button
                  variant="outline-warning"
                  size="sm"
                  type="button"
                  onClick={handleSeriesUpdate}
                >
                  Update Series
                </Button>
              </Col>
            </Row>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h5 className="d-inline">Series Name : </h5>
                <p className="d-inline">{series.name}</p>
              </ListGroup.Item>
              <ListGroup.Item>
                <h5 className="d-inline">Series Field : </h5>
                <p className="d-inline">{series.field}</p>
              </ListGroup.Item>
              <ListGroup.Item>
                <h5 className="d-inline">Series Levels : </h5>
                <p className="d-inline">{series.levels.length}</p>
              </ListGroup.Item>
            </ListGroup>
            <ListGroup.Item>
              <Link
                to={`/serieses/${seriesId}/levels/add`}
                className="btn btn-success mt-3"
              >
                Add New Level
              </Link>
            </ListGroup.Item>
          </Col>
          {series && series.levels && series.levels.length > 0 && (
            <Col md={8}>
              <Table hover responsive bordered striped size="sm">
                <thead>
                  <tr>
                    <th>Level Name</th>
                    <th>Level Time</th>
                    <th>Level Fees</th>
                    <th>Level Book Fees</th>
                    <th>Level Salary</th>
                    <th>Level Students</th>
                    <th>Level Teachers</th>
                  </tr>
                </thead>
                <tbody>
                  {series &&
                    series.levels &&
                    series.levels.length > 0 &&
                    series.levels.map(level => (
                      <tr key={level._id}>
                        <td>
                          <Link
                            to={`/serieses/${seriesId}/levels/${level._id}`}
                          >
                            <strong>{level.levelName}</strong>
                          </Link>
                        </td>
                        <td>{level.levelTime}</td>
                        <td>{level.levelFees}</td>
                        <td>{level.levelBookFees}</td>
                        <td>{level.levelSalary}</td>
                        <td>
                          {
                            <p className="d-inline">
                              {level.levelStudents.length}
                              {level.levelStudents.length > 1
                                ? " students"
                                : " student"}
                            </p>
                          }
                        </td>
                        <td>
                          {
                            <p className="d-inline">
                              {level.levelTeachers.length}
                              {level.levelTeachers.length > 1
                                ? " teachers"
                                : " teacher"}
                            </p>
                          }
                        </td>
                      </tr>
                    ))}
                </tbody>
              </Table>
            </Col>
          )}
        </Row>
      </Container>
    )
  )
}

export default SeriesInfoScreen
