import { ADD_STUDENT_RESET } from "../constants/studentConstants"
import {
  GET_TEACHERS_FAIL,
  GET_TEACHERS_SUCCESS,
  GET_TEACHERS_REQUEST,
  UPDATE_TEACHER_REQUEST,
  UPDATE_TEACHER_SUCCESS,
  UPDATE_TEACHER_FAIL,
  UPDATE_TEACHER_RESET,
  DELETE_TEACHER_REQUEST,
  DELETE_TEACHER_SUCCESS,
  DELETE_TEACHER_RESET,
  TEACHER_DETAILS_REQUEST,
  TEACHER_DETAILS_SUCCESS,
  TEACHER_DETAILS_FAIL,
  ADD_TEACHER_REQUEST,
  ADD_TEACHER_SUCCESS,
  ADD_TEACHER_FAIL,
  GET_TEACHERS_REPORTS_REQUEST,
  GET_TEACHERS_REPORTS_SUCCESS,
  GET_TEACHERS_REPORTS_FAIL,
} from "../constants/teachersConstants"

export const teacherListReducer = (state = { teachers: [] }, action) => {
  switch (action.type) {
    case GET_TEACHERS_REQUEST:
      return { loading: true }
    case GET_TEACHERS_SUCCESS:
      return {
        loading: false,
        teachers: action.payload,
      }
    case GET_TEACHERS_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

export const teacherDetailsReducer = (state = {}, action) => {
  switch (action.type) {
    case TEACHER_DETAILS_REQUEST:
      return {
        loading: true,
      }
    case TEACHER_DETAILS_SUCCESS:
      return {
        loading: false,
        teacher: action.payload,
      }
    case TEACHER_DETAILS_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

export const teacherUpdateReducer = (state = {}, action) => {
  switch (action.type) {
    case UPDATE_TEACHER_REQUEST:
      return {
        loading: true,
      }
    case UPDATE_TEACHER_SUCCESS:
      return {
        loading: false,
        success: true,
        teacher: action.payload,
      }
    case UPDATE_TEACHER_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    case UPDATE_TEACHER_RESET:
      return {}
    default:
      return state
  }
}

export const teacherDeleteReducer = (state = {}, action) => {
  switch (action.type) {
    case DELETE_TEACHER_REQUEST:
      return {
        loading: true,
      }
    case DELETE_TEACHER_SUCCESS:
      return {
        loading: false,
        success: true,
      }
    case UPDATE_TEACHER_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    case DELETE_TEACHER_RESET:
      return {}
    default:
      return state
  }
}

export const teacherAddReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_TEACHER_REQUEST:
      return {
        loading: true,
      }
    case ADD_TEACHER_SUCCESS:
      return {
        loading: false,
        success: true,
        teacher: action.payload,
      }
    case ADD_TEACHER_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    case ADD_STUDENT_RESET:
      return {}
    default:
      return state
  }
}

export const teachersReportReducer = (state = { teachers: [] }, action) => {
  switch (action.type) {
    case GET_TEACHERS_REPORTS_REQUEST:
      return {
        loading: true,
      }
    case GET_TEACHERS_REPORTS_SUCCESS:
      return {
        loading: false,
        teachers: action.payload,
      }
    case GET_TEACHERS_REPORTS_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}
