import React from "react"
import { Routes, Route } from "react-router-dom"

import Header from "./components/header/Header"
import AddStudentScreen from "./screens/AddStudentScreen"
import HomeScreen from "./screens/HomeScreen"
import AddTeacherScreen from "./screens/AddTeacherScreen"
import StudentsScreen from "./screens/StudentsScreen"
import StudentEditScreen from "./screens/StudentEditScreen"
import TeachersScreen from "./screens/TeachersScreen"
import OverviewScreen from "./screens/OverviewScreen"
import StudentsReportsScreen from "./screens/StudentsReportsScreen"
import TeachersReportsScreen from "./screens/TeachersReportsScreen"
import UserSignUpScreen from "./screens/UserSignupScreen"
import ProfileScreen from "./screens/ProfileScreen"
import UserLoginScreen from "./screens/UserLoginScreen"
import TeacherEditScreen from "./screens/TeacherEditScreen"
import UsersListScreen from "./screens/UsersListScreen"
import UserEditScreen from "./screens/UserEditScreen"
import SeriesScreen from "./screens/SeriesScreen"
import SeriesInfoScreen from "./screens/SeriesInfoScreen"
import SeriesLevelScreen from "./screens/SeriesLevelsScreen"
import AddSeriesScreen from "./screens/AddSeriesScreen"
import SeriesEditScreen from "./screens/SeriesEditScreen"
import LevelStudentAddScreen from "./screens/LevelStudentAddScreen"
import LevelStudentsListScreen from "./screens/LevelStudentsListScreen"
import LevelTeacherAddScreen from "./screens/LevelTeacherAddScreen"
import LevelTeachersListScreen from "./screens/LevelTeachersListScreen"
import AddLevelScreen from "./screens/AddLevelScreen"
import SeriesLevelEditScreen from "./screens/SeriesLevelEditScreen"

const App = () => {
  return (
    <>
      <Header />
      <main>
        <Routes>
          <Route path="" element={<HomeScreen />}></Route>
          <Route
            path="students/reports/:timePeriod"
            element={<StudentsReportsScreen />}
          ></Route>
          <Route path="students" element={<StudentsScreen />}></Route>
          <Route
            path="students/search/:keyword"
            element={<StudentsScreen />}
          ></Route>
          <Route
            path="students/page/:pageNumber"
            element={<StudentsScreen />}
          ></Route>
          <Route
            path="students/search/:keyword/page/:pageNumber"
            element={<StudentsScreen />}
          ></Route>
          <Route path="add/student" element={<AddStudentScreen />}></Route>
          <Route
            path="students/:studentId/edit"
            element={<StudentEditScreen />}
          ></Route>

          <Route
            path="teachers/:teacherId/edit"
            element={<TeacherEditScreen />}
          ></Route>
          <Route
            path="teachers/reports"
            element={<TeachersReportsScreen />}
          ></Route>
          <Route path="add/teacher" element={<AddTeacherScreen />}></Route>
          <Route path="teachers" element={<TeachersScreen />}></Route>

          <Route path="overview" element={<OverviewScreen />}></Route>

          <Route path="users" element={<UsersListScreen />}></Route>
          <Route path="users/:userId/edit" element={<UserEditScreen />}></Route>

          <Route path="signup" element={<UserSignUpScreen />}></Route>
          <Route path="login" element={<UserLoginScreen />}></Route>
          <Route path="profile" element={<ProfileScreen />}></Route>

          <Route path="serieses" element={<SeriesScreen />}></Route>
          <Route path="add/series" element={<AddSeriesScreen />}></Route>
          <Route
            path="serieses/:seriesId/edit"
            element={<SeriesEditScreen />}
          ></Route>
          <Route
            path="serieses/:seriesId"
            element={<SeriesInfoScreen />}
          ></Route>
          <Route
            path="serieses/:seriesId/levels/add"
            element={<AddLevelScreen />}
          ></Route>
          <Route
            path="serieses/:seriesId/levels/:levelId"
            element={<SeriesLevelScreen />}
          ></Route>
          <Route
            path="serieses/:seriesId/levels/:levelId/edit"
            element={<SeriesLevelEditScreen />}
          ></Route>

          <Route
            path="serieses/:seriesId/levels/:levelId/students"
            element={<LevelStudentsListScreen />}
          ></Route>
          <Route
            path="serieses/:seriesId/levels/:levelId/add/student"
            element={<LevelStudentAddScreen />}
          ></Route>

          <Route
            path="serieses/:seriesId/levels/:levelId/teachers"
            element={<LevelTeachersListScreen />}
          ></Route>
          <Route
            path="serieses/:seriesId/levels/:levelId/add/teacher"
            element={<LevelTeacherAddScreen />}
          ></Route>
        </Routes>
      </main>
    </>
  )
}

export default App
