import React, { useEffect } from "react"
import { Container } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"
import {
  listStudentDailyReports,
  listStudentMonthlyReports,
  listStudentWeeklyReports,
  listStudentYearlyReports,
} from "../actions/studentsAction"
import ReportsList from "../components/ReportsList"

const StudentsReportScreen = () => {
  const { timePeriod } = useParams()

  const dispatch = useDispatch()

  const navigate = useNavigate()

  const studentsYearlyReports = useSelector(
    state => state.studentsYearlyReports
  )
  const studentsMonthlyReports = useSelector(
    state => state.studentsMonthlyReports
  )
  const studentsWeeklyReports = useSelector(
    state => state.studentsWeeklyReports
  )
  const studentsDailyReports = useSelector(state => state.studentsDailyReports)

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  useEffect(() => {
    if (!userInfo && !userInfo.isAdmin) {
      navigate("/login")
    }
    if (timePeriod === "yearly") {
      dispatch(listStudentYearlyReports())
    } else if (timePeriod === "monthly") {
      dispatch(listStudentMonthlyReports())
    } else if (timePeriod === "weekly") {
      dispatch(listStudentWeeklyReports())
    } else {
      dispatch(listStudentDailyReports())
    }
  }, [dispatch, timePeriod, navigate, userInfo])

  let content

  if (timePeriod === "yearly") {
    content = (
      <ReportsList
        timePeriod={timePeriod}
        studentsData={studentsYearlyReports}
      />
    )
  } else if (timePeriod === "monthly") {
    content = (
      <ReportsList
        timePeriod={timePeriod}
        studentsData={studentsMonthlyReports}
      />
    )
  } else if (timePeriod === "weekly") {
    content = (
      <ReportsList
        timePeriod={timePeriod}
        studentsData={studentsWeeklyReports}
      />
    )
  } else {
    content = (
      <ReportsList
        timePeriod={timePeriod}
        studentsData={studentsDailyReports}
      />
    )
  }

  return <Container>{content}</Container>
}

export default StudentsReportScreen
