import React from "react"
import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { LinkContainer } from "react-router-bootstrap"
import { useNavigate } from "react-router-dom"
import { logout } from "../../actions/usersAction"

const Header = () => {
  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  const dispatch = useDispatch()

  const navigate = useNavigate()

  const handleLogout = () => {
    if (window.confirm("Want to logout ?")) {
      dispatch(logout())
    }
    navigate("/")
  }

  return (
    <header style={{ marginBottom: 75 }}>
      <Navbar
        fixed="top"
        bg="primary"
        variant="dark"
        expand="lg"
        collapseOnSelect
      >
        <Container>
          <LinkContainer to="/">
            <Navbar.Brand>KAMIL INSTITUTE</Navbar.Brand>
          </LinkContainer>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav" className="centerNavbar">
            <Nav className="ml-auto">
              {userInfo && userInfo.name ? (
                <NavDropdown title={userInfo.name} id="profileMenu">
                  <LinkContainer to="/profile">
                    <NavDropdown.Item>Profile</NavDropdown.Item>
                  </LinkContainer>
                  <NavDropdown.Item onClick={handleLogout}>
                    Logout
                  </NavDropdown.Item>
                </NavDropdown>
              ) : (
                <LinkContainer to="/login">
                  <Nav.Link>LOGIN</Nav.Link>
                </LinkContainer>
              )}

              {userInfo && userInfo.isAdmin && (
                <LinkContainer to="/users">
                  <Nav.Link>USERS</Nav.Link>
                </LinkContainer>
              )}

              <NavDropdown title="STUDENTS" id="studentsMenu">
                <LinkContainer to="/students">
                  <NavDropdown.Item>Students</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to="/add/student">
                  <NavDropdown.Item>Add Student</NavDropdown.Item>
                </LinkContainer>
              </NavDropdown>

              <NavDropdown title="TEACHERS" id="teachersMenu">
                <LinkContainer to="/teachers">
                  <NavDropdown.Item>Teachers</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to="/add/teacher">
                  <NavDropdown.Item>Add Teacher</NavDropdown.Item>
                </LinkContainer>
              </NavDropdown>

              {userInfo && userInfo.isAdmin && (
                <NavDropdown title="REPORTS">
                  <LinkContainer to="/students/reports/yearly">
                    <NavDropdown.Item>Last Year</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/students/reports/monthly">
                    <NavDropdown.Item>Last Month</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/students/reports/weekly">
                    <NavDropdown.Item>Last Week</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/students/reports/daily">
                    <NavDropdown.Item>Today</NavDropdown.Item>
                  </LinkContainer>
                </NavDropdown>
              )}

              <NavDropdown title="SERIESES" id="seriesesMenu">
                <LinkContainer to="/serieses">
                  <NavDropdown.Item>Serieses</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to="/add/series">
                  <NavDropdown.Item>Add Series</NavDropdown.Item>
                </LinkContainer>
              </NavDropdown>

              {/* <LinkContainer to="/overview">
                <Nav.Link>OVERVIEW</Nav.Link>
              </LinkContainer> */}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  )
}

export default Header
