import React, { useEffect } from "react"
import { Button, Container, Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate } from "react-router-dom"

import { adminDeleteUser, listUsers } from "../actions/usersAction"
import Loader from "../components/Loader"
import Message from "../components/Message"

const UsersListScreen = () => {
  const dispatch = useDispatch()

  const navigate = useNavigate()

  const userList = useSelector(state => state.userList)
  const { loading, error, users } = userList

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  const adminUserDelete = useSelector(state => state.adminUserDelete)
  const { success } = adminUserDelete

  useEffect(() => {
    if (!userInfo.isAdmin) {
      alert("You are not an admin user cannot visit users page")
      navigate("/")
    }
    if (success) {
      dispatch(listUsers())
    }
    dispatch(listUsers())
  }, [dispatch, navigate, userInfo, success])

  const handleUserEdit = userId => {
    navigate(`/users/${userId}/edit`)
  }
  const handleUserDelete = userId => {
    if (window.confirm("Are you sure")) {
      dispatch(adminDeleteUser(userId))
    }
  }

  return (
    <Container>
      <h3 className="my-4">Users</h3>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <Table hover striped responsive bordered className="table-sm">
          <thead>
            <tr>
              <th>ID</th>
              <th>NAME</th>
              <th>EMAIL</th>
              <th>ADMIN</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {users &&
              users.map(user => (
                <tr key={user._id}>
                  <td>{user._id}</td>
                  <td>{user.name}</td>
                  <td>{user.email}</td>
                  <td>
                    {user.isAdmin ? (
                      <i className="fa fa-check" style={{ color: "green" }}></i>
                    ) : (
                      <i className="fa fa-times" style={{ color: "red" }}></i>
                    )}
                  </td>
                  {user._id === userInfo._id ? (
                    <td></td>
                  ) : (
                    <td>
                      <Button
                        type="button"
                        variant="primary"
                        size="sm"
                        className="mx-1"
                        onClick={() => handleUserEdit(user._id)}
                      >
                        <i className="fa fa-edit"></i>
                      </Button>
                      <Button
                        type="button"
                        variant="danger"
                        size="sm"
                        className="mx-1"
                        onClick={() => handleUserDelete(user._id)}
                      >
                        <i className="fa fa-trash"></i>
                      </Button>
                    </td>
                  )}
                </tr>
              ))}
          </tbody>
        </Table>
      )}
    </Container>
  )
}

export default UsersListScreen
