import React, { useEffect } from "react"
import { Container, Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"

import { listTeacherReports } from "../actions/teacherAction"
import Loader from "../components/Loader"
import Message from "../components/Message"

const TeachersReportsScreen = () => {
  const dispatch = useDispatch()

  const teachersReport = useSelector(state => state.teachersReport)
  const { loading, error, teachers } = teachersReport

  useEffect(() => {
    dispatch(listTeacherReports())
  }, [dispatch])

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <Container>
      <h3>Teachers Current Month Report</h3>

      <Table striped bordered hover responsive>
        <thead>
          <tr>
            <th>ID</th>
            <th>NAME</th>
            <th>EMAIL</th>
            <th>PHONE NUMBER</th>
            <th>TEACHING FIELD</th>
            <th>ATTENDANCE TIME</th>
            <th>SALARY</th>
            <th>REGISTERED AT</th>
          </tr>
        </thead>

        <tbody>
          {teachers &&
            teachers.length > 0 &&
            teachers.map(teacher => (
              <tr key={teacher._id}>
                <td>{teacher._id.substring(20, 25)}</td>
                <td>{teacher.name}</td>
                <td>{teacher.email}</td>
                <td>{teacher.phoneNumber}</td>
                <td>{teacher.teachingField}</td>
                <td>{teacher.attendanceTime}</td>
                <td>{teacher.salary}</td>
                <td>{teacher.rolledAt.substring(0, 10)}</td>
              </tr>
            ))}
        </tbody>
      </Table>
    </Container>
  )
}

export default TeachersReportsScreen
