import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Form, Button, Row, Col, Container } from "react-bootstrap"
import { Link, useNavigate } from "react-router-dom"

import FormContainer from "../components/FormContainer"
import { addTeacher } from "../actions/teacherAction"
import Message from "../components/Message"
import Loader from "../components/Loader"
import { ADD_TEACHER_RESET } from "../constants/teachersConstants"

const AddTeacherScreen = () => {
  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [phoneNumber, setPhoneNumber] = useState("")

  const dispatch = useDispatch()

  const navigate = useNavigate()

  const teacherAdd = useSelector(state => state.teacherAdd)
  const { loading, error, success } = teacherAdd

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  useEffect(() => {
    if (!userInfo) {
      navigate("/login?redirect=add/teacher")
    }
    if (success) {
      dispatch({ type: ADD_TEACHER_RESET })
      navigate("/teachers")
    }
  }, [dispatch, success, navigate, userInfo])

  const handleSubmit = e => {
    e.preventDefault()

    dispatch(
      addTeacher({
        name,
        email,
        phoneNumber,
      })
    )
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <Container>
      <Row>
        <Col md={5}>
          <Link to="/" className="btn btn-sm btn-outline-secondary">
            Go Back
          </Link>
        </Col>
        <Col md={6}>
          <h3>Add Teacher</h3>
        </Col>
      </Row>
      <Form onSubmit={handleSubmit} className="mt-4">
        <Form.Group controlId="name" className="m-2">
          <Form.Label> Name </Form.Label>
          <Form.Control
            type="text"
            value={name}
            placeholder="Enter Name"
            onChange={e => setName(e.target.value)}
          ></Form.Control>
        </Form.Group>
        <Form.Group controlId="email" className="m-2">
          <Form.Label> Email </Form.Label>
          <Form.Control
            type="email"
            value={email}
            placeholder="Enter Email"
            onChange={e => setEmail(e.target.value)}
          ></Form.Control>
        </Form.Group>
        <Form.Group controlId="phoneNumber" className="m-2">
          <Form.Label>Phone Number</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter Phone Number"
            value={phoneNumber}
            onChange={e => setPhoneNumber(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Button type="submit" variant="success" className="my-4">
          Add Teacher
        </Button>
      </Form>
    </Container>
  )
}

export default AddTeacherScreen
