import React, { useEffect, useState } from "react"
import { Button, Container, Form } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate } from "react-router-dom"
import { updateSeriesById } from "../actions/seriesAction"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { UPDATE_SERIES_BY_ID_RESET } from "../constants/seriesConstants"

const SeriesEditScreen = () => {
  const [name, setName] = useState("")
  const [field, setField] = useState("")
  const [seriesStartDate, setSeriesStartDate] = useState("")
  const [transformedStartDate, setTransformedStartDate] = useState("")
  const [seriesEndDate, setSeriesEndDate] = useState("")
  const [transformedEndDate, setTransformedEndDate] = useState("")
  const [dailyStudyTime, setDailyStudyTime] = useState("")

  const navigate = useNavigate()

  const dispatch = useDispatch()

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { series } = seriesDetails

  const seriesUpdateById = useSelector(state => state.seriesUpdateById)
  const { success, loading, error } = seriesUpdateById

  useEffect(() => {
    if (success) {
      navigate("/serieses")
      dispatch({ type: UPDATE_SERIES_BY_ID_RESET })
    }

    if (series && series.name) {
      setName(series.name)
      setField(series.field)
      setDailyStudyTime(series.dailyStudyTime)
    }
  }, [navigate, dispatch, success, series])

  const startDateHandler = e => {
    const value = e.target.value
    setTransformedStartDate(value.split("-").join(","))
    setSeriesStartDate(value)
  }

  const endDateHandler = e => {
    const value = e.target.value
    setTransformedEndDate(value.split("-").join(","))
    setSeriesEndDate(value)
  }

  const handleSubmit = e => {
    e.preventDefault()

    dispatch(
      updateSeriesById({
        name,
        field,
        seriesStartDate: seriesStartDate && transformedStartDate,
        seriesEndDate: seriesEndDate && transformedEndDate,
        dailyStudyTime,
      })
    )
  }

  return loading ? (
    <Loader />
  ) : (
    <Container>
      <h3 className="my-4">Edit Series</h3>
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="name" className="my-3">
          <Form.Label>Series Name</Form.Label>
          <Form.Control
            type="text"
            required
            value={name}
            onChange={e => setName(e.target.value)}
            placeholder="Enter Series Name"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="field" className="my-3">
          <Form.Label>Series Field</Form.Label>
          <Form.Control
            type="text"
            required
            value={field}
            onChange={e => setField(e.target.value)}
            placeholder="Enter Series Field"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="seriesStartDate">
          <Form.Label>Series Start Date</Form.Label>
          <Form.Control
            type="date"
            value={seriesStartDate}
            onChange={startDateHandler}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="seriesEndDate">
          <Form.Label>Series End Date</Form.Label>
          <Form.Control
            type="date"
            value={seriesEndDate}
            onChange={endDateHandler}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="dailyStudyTime" className="my-3">
          <Form.Label>Daily Study Time</Form.Label>
          <Form.Control
            type="text"
            required
            value={dailyStudyTime}
            onChange={e => setDailyStudyTime(e.target.value)}
            placeholder="Select Daily Study Time"
          ></Form.Control>
        </Form.Group>

        {error && <Message variant="danger">{error}</Message>}

        <Button type="submit" variant="success" className="mt-4">
          Update
        </Button>
      </Form>
    </Container>
  )
}

export default SeriesEditScreen
