import React from "react"
import { Card } from "react-bootstrap"
import { Link } from "react-router-dom"

const SeriesDetails = ({
  seriesId,
  name,
  seriesStartDate,
  seriesEndDate,
  dailyStudyTime,
}) => {
  return (
    <Link to={seriesId} style={{ textDecoration: "none", color: "black" }}>
      <Card className="p-3" style={{ boxShadow: "2px 3px 50px #3498db" }}>
        <Card.Title>
          <h5>{name}</h5>
        </Card.Title>
        <Card.Text as="p">
          <strong>Series Start Date : </strong>
          {seriesStartDate.substring(0, 10)}
        </Card.Text>
        <Card.Text as="p">
          <strong>Series End Date : </strong>
          {seriesEndDate.substring(0, 10)}
        </Card.Text>
        <Card.Text as="p">
          <strong>Daily Study Time : </strong>
          {dailyStudyTime}
        </Card.Text>
      </Card>
    </Link>
  )
}

export default SeriesDetails
