import React, { useEffect, useRef, useState } from "react"
import { Button, Container, Form, Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useParams } from "react-router-dom"
import {
  salaryPaidToLevelTeacher,
  getSeriesLevelDetails,
} from "../actions/seriesAction"
import Loader from "../components/Loader"
import Message from "../components/Message"

const LevelTeachersListScreen = () => {
  const { levelId, seriesId } = useParams()

  const [currentTeacher, setCurrentTeacher] = useState({})
  const [isSalaryPaidSuccess, setIsSalaryPaidSuccess] = useState(false)

  const dispatch = useDispatch()

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { series } = seriesDetails

  const seriesLevelDetails = useSelector(state => state.seriesLevelDetails)
  const { loading, error, level } = seriesLevelDetails

  const { teacherId, isChecked } = currentTeacher
  useEffect(() => {
    dispatch(getSeriesLevelDetails(seriesId, levelId))

    if (isSalaryPaidSuccess) {
      dispatch(getSeriesLevelDetails(seriesId, levelId))
    }
  }, [dispatch, seriesId, levelId, isSalaryPaidSuccess, teacherId, isChecked])

  const findSalaryPaid = tea => {
    return (
      level &&
      tea.salaries.find(sal => {
        const salary = sal.level.toString() === levelId.toString() ? sal : {}
        return salary.isPaid
      })
    )
  }

  const formSubmitRef = useRef()

  const handleCheckChange = (e, teacherId) => {
    setCurrentTeacher({
      teacherId,
      isChecked: e.target.checked,
    })

    formSubmitRef.current.click()
  }

  const handleSalarySubmission = event => {
    event.preventDefault()

    setIsSalaryPaidSuccess(prevValue => !prevValue)

    if (currentTeacher && currentTeacher.teacherId) {
      dispatch(
        salaryPaidToLevelTeacher(seriesId, levelId, currentTeacher.teacherId, {
          isPaid: currentTeacher.isChecked,
        })
      )
    }
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    level && (
      <Container>
        <h3 className="mb-4 mt-1">
          {series && series.name} Series {level && level.levelName} Teachers
          List
        </h3>
        <Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>NAME</th>
              <th>EMAIL</th>
              <th>PHONE NUMBER</th>
              <th>REGISTERED AT</th>
              <th>LEVEL SALARY</th>
              <th>IS PAID</th>
            </tr>
          </thead>
          <tbody>
            {level &&
              level.levelTeachers &&
              level.levelTeachers.length > 0 &&
              level.levelTeachers.map(tea => (
                <tr key={tea._id}>
                  <td>{tea.name}</td>
                  <td>{tea.email}</td>
                  <td>{tea.phoneNumber}</td>
                  <td>{tea.createdAt.substring(0, 10)}</td>
                  <td>{level.levelSalary}</td>
                  <td>
                    <>
                      {findSalaryPaid(level && tea) ? (
                        <i
                          className="fa fa-check d-inline"
                          style={{ color: "green" }}
                        ></i>
                      ) : (
                        <i className="fa fa-times" style={{ color: "red" }}></i>
                      )}
                      <Form
                        className="d-inline"
                        onSubmit={handleSalarySubmission}
                      >
                        <Form.Check
                          checked={findSalaryPaid(level && tea)}
                          className="d-inline-block mx-3"
                          onChange={e => handleCheckChange(e, tea._id)}
                        ></Form.Check>
                        <Button
                          ref={formSubmitRef}
                          style={{ display: "none" }}
                          type="submit"
                        ></Button>
                      </Form>
                    </>
                  </td>
                </tr>
              ))}
          </tbody>
        </Table>
      </Container>
    )
  )
}

export default LevelTeachersListScreen
