import React, { useEffect, useState } from "react"
import { Button, Container, Form } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"
import { addLevelToSeries, getSeriesDetails } from "../actions/seriesAction"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { ADD_SERIES_LEVEL_RESET } from "../constants/seriesConstants"

const AddLevelScreen = () => {
  const [levelName, setLevelName] = useState()
  const [levelTime, setLevelTime] = useState()
  const [levelFees, setLevelFees] = useState(0)
  const [levelBookFees, setLevelBookFees] = useState(0)
  const [levelSalary, setLevelSalary] = useState(0)

  const navigate = useNavigate()

  const { seriesId } = useParams()

  const dispatch = useDispatch()

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { series } = seriesDetails

  const seriesLevelAdd = useSelector(state => state.seriesLevelAdd)
  const { loading, error, success } = seriesLevelAdd

  useEffect(() => {
    dispatch(getSeriesDetails(seriesId))

    if (success) {
      dispatch({ type: ADD_SERIES_LEVEL_RESET })
      navigate(`/serieses/${seriesId}`)
    }
  }, [dispatch, navigate, seriesId, success])

  const handleSubmit = e => {
    e.preventDefault()

    dispatch(
      addLevelToSeries(seriesId, {
        level: {
          levelName,
          levelTime,
          levelFees,
          levelBookFees,
          levelSalary,
        },
      })
    )
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <Container>
      <h3 className="mb-4">Add New Level To {series && series.name} Series </h3>
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="levelName">
          <Form.Label>Level Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter Level Name"
            value={levelName}
            onChange={e => setLevelName(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelTime">
          <Form.Label>Level Time</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter Level Time"
            value={levelTime}
            onChange={e => setLevelTime(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelFees">
          <Form.Label>Level Fees</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter Level Fees"
            value={levelFees}
            onChange={e => setLevelFees(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelBookFees">
          <Form.Label>Level Book Fees</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter Level Book Fees"
            value={levelBookFees}
            onChange={e => setLevelBookFees(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelSalary">
          <Form.Label>Level Salary</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter Level Salary"
            value={levelSalary}
            onChange={e => setLevelSalary(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Button type="submit" variant="success" className="mt-3">
          Add Level
        </Button>
      </Form>
    </Container>
  )
}

export default AddLevelScreen
