import React, { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Table, Button, Container, Row, Col } from "react-bootstrap"
import { useNavigate, useParams } from "react-router-dom"

import { deleteStudent, listStudents } from "../actions/studentsAction"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { DELETE_STUDENT_RESET } from "../constants/studentConstants"
import Search from "../components/Search"
import Paginate from "../components/Paginate"

const StudentList = () => {
  const dispatch = useDispatch()

  const navigate = useNavigate()

  const { keyword, pageNumber } = useParams()

  const studentList = useSelector(state => state.studentList)
  const { loading, error, students, page, pages } = studentList

  const studentDelete = useSelector(state => state.studentDelete)
  const { loading: deleteLoading, error: deleteError, success } = studentDelete

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  useEffect(() => {
    if (!userInfo) {
      navigate("/login?redirect=students")
    }
    dispatch(listStudents(keyword, pageNumber))

    if (success) {
      dispatch(listStudents(keyword, pageNumber))
      dispatch({ type: DELETE_STUDENT_RESET })
    }
  }, [dispatch, success, keyword, pageNumber, navigate, userInfo])

  const handleStudentDelete = studentId => {
    if (window.confirm("Are you sure ?")) {
      dispatch(deleteStudent(studentId))
    }
  }
  const handleStudentUpdate = studentId => {
    navigate(`/students/${studentId}/edit`)
  }

  return loading ? (
    <Loader />
  ) : deleteLoading ? (
    <Loader />
  ) : (
    <Container fluid>
      <Row className="mb-3">
        <Col md={5}>
          <Search pageName="students" />
        </Col>
        <Col md={5}>
          <h2>Students</h2>
        </Col>
        <Col md={2}>
          <Button
            type="button"
            variant="info"
            size="sm"
            onClick={() => window.print()}
          >
            Print
          </Button>
        </Col>
      </Row>
      {error ? (
        <Message variant="danger">{error}</Message>
      ) : deleteError ? (
        <Message variant="danger">{deleteError}</Message>
      ) : (
        <Table
          striped
          bordered
          hover
          responsive
          size="sm"
          className="print-container"
        >
          <thead>
            <tr>
              <th>Id</th>
              <th>Name</th>
              <th>F/Name</th>
              <th>Gender</th>
              <th>Phone Number</th>
              <th>Registered At</th>
              <th>Registration Fees</th>
              <th>Card Fees</th>
              <th className="avoid-print"></th>
            </tr>
          </thead>

          <tbody>
            {students &&
              students.length > 0 &&
              students.map(student => (
                <tr key={student._id}>
                  <td>{student._id.substring(16, 25)}</td>
                  <td>{student.name}</td>
                  <td>{student.fatherName}</td>
                  <td>{student.gender}</td>
                  <td>{student.phoneNumber}</td>
                  <td>{student.createdAt.substring(0, 10)}</td>
                  <td>
                    {student.registrationFees.feesAmount}

                    <div className="d-inline-block mx-2 ">
                      {student.registrationFees.isPaid ? (
                        <i
                          className="fa fa-check"
                          style={{ color: "green" }}
                        ></i>
                      ) : (
                        <i className="fa fa-times" style={{ color: "red" }}></i>
                      )}
                    </div>
                    <div className="d-inline-block mx-2">
                      {student.registrationFees.isPaid &&
                        student.registrationFees.paidAt.substring(0, 10)}
                    </div>
                  </td>
                  <td>
                    {student.cardFees.feesAmount}

                    <div className="d-inline-block mx-2 ">
                      {student.cardFees.isPaid ? (
                        <i
                          className="fa fa-check"
                          style={{ color: "green" }}
                        ></i>
                      ) : (
                        <i className="fa fa-times" style={{ color: "red" }}></i>
                      )}
                    </div>
                    <div className="d-inline-block mx-2">
                      {student.cardFees.isPaid &&
                        student.cardFees.paidAt.substring(0, 10)}
                    </div>
                  </td>

                  <td className="avoid-print">
                    <Button
                      type="button"
                      variant="warning"
                      className="mx-2 btn-sm"
                      onClick={() => handleStudentUpdate(student._id)}
                    >
                      <i className="fa fa-edit"></i>
                    </Button>

                    <Button
                      type="button"
                      variant="danger"
                      className="mx-2 btn-sm"
                      onClick={() => handleStudentDelete(student._id)}
                    >
                      <i className="fa fa-trash"></i>
                    </Button>
                  </td>
                </tr>
              ))}
          </tbody>
        </Table>
      )}
      <Paginate
        keyword={keyword && keyword}
        pageName="students"
        pages={pages}
        page={page}
      />
    </Container>
  )
}

export default StudentList
