import React, { useEffect, useState } from "react"
import _ from "lodash"
import {
  Col,
  Container,
  Row,
  Form,
  ListGroup,
  Table,
  Button,
} from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"
import {
  addTeacherToSeriesLevel,
  getSeriesDetails,
  getSeriesLevelDetails,
} from "../actions/seriesAction"
import { listTeachers } from "../actions/teacherAction"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { SERIES_LEVEL_ADD_TEACHER_RESET } from "../constants/seriesConstants"

const LevelTeacherAddScreen = () => {
  const [selectedTeachers, setSelectedTeachers] = useState([])
  const [formattedTeachers, setFormattedTeachers] = useState([])
  const [unExistTeachers, setUnExistTeachers] = useState([])

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { series } = seriesDetails

  const seriesLevelDetails = useSelector(state => state.seriesLevelDetails)
  const { level } = seriesLevelDetails

  const teacherList = useSelector(state => state.teacherList)
  const { teachers } = teacherList

  const { seriesId, levelId } = useParams()

  const dispatch = useDispatch()

  const navigate = useNavigate()

  useEffect(() => {
    dispatch(listTeachers())
    dispatch(getSeriesDetails(seriesId))
    dispatch(getSeriesLevelDetails(seriesId, levelId))
  }, [dispatch, seriesId, levelId])

  //  for checking the teachers existence in the level teachers
  useEffect(() => {
    if (teachers && teachers.length > 0 && level && level.levelTeachers) {
      const notInLevelTeachers = _.differenceBy(
        teachers,
        level.levelTeachers,
        "_id"
      )

      setUnExistTeachers(notInLevelTeachers)
    }
  }, [level, teachers])

  const handleTeacherChecked = (e, teacher) => {
    if (e.target.checked) {
      const existTeacher = selectedTeachers.find(
        tea => tea.id.toString() === teacher.id.toString()
      )
      if (!existTeacher) {
        setSelectedTeachers(prevTeachers => [...prevTeachers, teacher])
      } else {
        return
      }
    } else if (!e.target.checked) {
      const existTeacher = selectedTeachers.find(
        tea => tea.id.toString() === teacher.id.toString()
      )
      if (existTeacher) {
        const filteredSelectedTeachers = selectedTeachers.filter(
          tea => tea.id.toString() !== teacher.id.toString()
        )

        setSelectedTeachers(filteredSelectedTeachers)
      } else {
        return
      }
    }
  }

  const seriesLevelTeacherAdd = useSelector(
    state => state.seriesLevelTeacherAdd
  )
  const {
    success: teacherAddSuccess,
    loading: teacherAddLoading,
    error: teacherAddError,
  } = seriesLevelTeacherAdd

  useEffect(() => {
    if (teacherAddSuccess) {
      dispatch({ type: SERIES_LEVEL_ADD_TEACHER_RESET })
      navigate(`/serieses/${seriesId}/levels/${levelId}/teachers`)
    }
  }, [
    dispatch,
    navigate,
    teacherAddSuccess,
    selectedTeachers,
    seriesId,
    levelId,
  ])

  const handleAddTeachers = async e => {
    e.preventDefault()

    selectedTeachers.forEach(tea => {
      return setFormattedTeachers(prevTeachers => [...prevTeachers, tea.id])
    })

    if (
      formattedTeachers &&
      formattedTeachers.length === selectedTeachers.length
    ) {
      dispatch(
        addTeacherToSeriesLevel(seriesId, levelId, {
          levelTeachers: formattedTeachers,
        })
      )
    }
  }

  return teacherAddLoading ? (
    <Loader />
  ) : teacherAddError ? (
    <Message variant="danger">{teacherAddError}</Message>
  ) : (
    series &&
    level && (
      <Container>
        <h4>
          Add Teacher to {series.name} series in {level.levelName}
        </h4>

        <Row>
          <Col md={6}>
            <div
              className="mt-4"
              style={{ overflowY: "scroll", height: "70vh" }}
            >
              <Form.Label as="legend">Select teachers to add</Form.Label>
              <Table size="sm" borderless striped responsive>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>PHONE NUMBER</th>
                    <th>Select</th>
                  </tr>
                </thead>
                <tbody>
                  {unExistTeachers &&
                    unExistTeachers.length > 0 &&
                    unExistTeachers.map(teacher => (
                      <tr key={teacher._id}>
                        <td>{teacher.name}</td>
                        <td>{teacher.phoneNumber}</td>
                        <td>
                          <Form.Group>
                            <Form.Check
                              type="checkbox"
                              onChange={e =>
                                handleTeacherChecked(e, {
                                  id: teacher._id,
                                  name: teacher.name,
                                  phoneNumber: teacher.phoneNumber,
                                })
                              }
                            ></Form.Check>
                          </Form.Group>
                        </td>
                      </tr>
                    ))}
                </tbody>
              </Table>
            </div>
          </Col>
          <Col md={6}>
            <ListGroup
              className="mt-4"
              style={{ overflowY: "scroll", height: "65vh" }}
            >
              <ListGroup.Item>
                <strong>Selected Teachers</strong>
              </ListGroup.Item>
              <Table size="sm" hover bordered>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>PHONE NUMBER</th>
                  </tr>
                </thead>
                <tbody>
                  {selectedTeachers.length > 0 &&
                    selectedTeachers.map(teacher => (
                      <tr key={teacher.id}>
                        <td>{teacher.name}</td>
                        <td>{teacher.phoneNumber}</td>
                      </tr>
                    ))}
                </tbody>
              </Table>
            </ListGroup>
            <Form onSubmit={handleAddTeachers}>
              <Button type="submit" variant="outline-success" size="sm">
                Add teachers
              </Button>
            </Form>
          </Col>
        </Row>
      </Container>
    )
  )
}

export default LevelTeacherAddScreen
