import React, { useEffect } from "react"
import { Container, Row, Col, ListGroup } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { Link, useNavigate } from "react-router-dom"

import Loader from "../components/Loader"
import Message from "../components/Message"
import { listStudents } from "../actions/studentsAction"
import { listTeachers } from "../actions/teacherAction"

const OverviewScreen = () => {
  const dispatch = useDispatch()

  const navigate = useNavigate()

  const studentList = useSelector(state => state.studentList)
  const { students, loading, error } = studentList

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  const teacherList = useSelector(state => state.teacherList)
  const { teachers, loading: teacherLoading, error: teacherError } = teacherList

  useEffect(() => {
    if (!userInfo) {
      navigate("/login")
    }
    dispatch(listStudents)
    dispatch(listTeachers())
  }, [dispatch, navigate, userInfo])

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : teacherLoading ? (
    <Loader />
  ) : teacherError ? (
    <Message variant="danger">{teacherError}</Message>
  ) : (
    <Container>
      <h2 className="text-center mb-4 pb-1">Overview</h2>
      <Row>
        <Col md={6}>
          <ListGroup
            variant="flush"
            style={{ borderRadius: 10, boxShadow: "3px 1px 9px #3498db" }}
          >
            <ListGroup.Item>
              <h4>Students</h4>
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Students Count : </strong>
              {students && students.length > 0 && students.length}
              {students.length > 1 ? " students" : " student"}
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Students Fesses : </strong>
              {students &&
                students.length > 0 &&
                students.reduce((acc, student) => acc + student.fees, 0)}
              AF
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Paid Students : </strong>
              {students &&
                students.length > 0 &&
                students.filter(student => student.isPaid === true).length}
              {students.filter(student => student.isPaid === true).length > 1
                ? " students"
                : " student"}
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Unpaid Students : </strong>
              {students &&
                students.length > 0 &&
                students.filter(student => student.isPaid !== true).length}
              {students.filter(student => student.isPaid !== true).length > 1
                ? " students"
                : " student"}
            </ListGroup.Item>
          </ListGroup>
        </Col>
        <Col md={1}></Col>
        <Col md={5}>
          <ListGroup
            variant="flush"
            style={{ borderRadius: 10, boxShadow: "3px 1px 9px #3498db" }}
          >
            <ListGroup.Item>
              <h4>Overall Statistics</h4>
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Income : </strong>
              {students &&
                students.length > 0 &&
                students.reduce((acc, student) => acc + student.fees, 0)}
              AF
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Outcome : </strong>
              {teachers &&
                teachers.length > 0 &&
                teachers.reduce((acc, teacher) => acc + teacher.salary, 0)}
              AF
            </ListGroup.Item>
            {userInfo && userInfo.isAdmin && (
              <ListGroup.Item>
                <Row>
                  <Col md={6}>
                    <Link
                      to="/students/reports"
                      className="btn btn-info btn-sm "
                    >
                      Students Monthly Report <i className="fa fa-book"></i>
                    </Link>
                  </Col>
                  <Col md={6}>
                    <Link
                      to="/teachers/reports"
                      className="btn btn-info btn-sm "
                    >
                      Teachers Monthly Report <i className="fa fa-book"></i>
                    </Link>
                  </Col>
                </Row>
              </ListGroup.Item>
            )}
          </ListGroup>
        </Col>
      </Row>

      <Row className="mt-5">
        <Col md={6}>
          <ListGroup
            variant="flush"
            style={{ borderRadius: 10, boxShadow: "3px 1px 9px #3498db" }}
          >
            <ListGroup.Item>
              <h4>Teachers</h4>
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Teachers Count : </strong>
              {teachers && teachers.length > 0 && teachers.length}
              {teachers.length > 1 ? " teachers" : " teacher"}
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Teachers Salaries : </strong>
              {teachers &&
                teachers.length > 0 &&
                teachers.reduce((acc, teacher) => acc + teacher.salary, 0)}
              AF
            </ListGroup.Item>
          </ListGroup>
        </Col>
      </Row>
    </Container>
  )
}

export default OverviewScreen
