import axios from "axios"

import {
  GET_STUDENTS_REQUEST,
  GET_STUDENTS_SUCCESS,
  GET_STUDENTS_FAIL,
  DELETE_STUDENT_REQUEST,
  DELETE_STUDENT_SUCCESS,
  DELETE_STUDENT_FAIL,
  UPDATE_STUDENT_REQUEST,
  UPDATE_STUDENT_SUCCESS,
  UPDATE_STUDENT_FAIL,
  STUDENT_DETAILS_REQUEST,
  STUDENT_DETAILS_SUCCESS,
  STUDENT_DETAILS_FAIL,
  ADD_STUDENT_REQUEST,
  ADD_STUDENT_SUCCESS,
  ADD_STUDENT_FAIL,
  STUDENTS_REPORTS_YEARLY_REQUEST,
  STUDENTS_REPORTS_YEARLY_SUCCESS,
  STUDENTS_REPORTS_YEARLY_FAIL,
  STUDENTS_REPORTS_MONTHLY_REQUEST,
  STUDENTS_REPORTS_MONTHLY_SUCCESS,
  STUDENTS_REPORTS_MONTHLY_FAIL,
  STUDENTS_REPORTS_WEEKLY_REQUEST,
  STUDENTS_REPORTS_WEEKLY_SUCCESS,
  STUDENTS_REPORTS_WEEKLY_FAIL,
  STUDENTS_REPORTS_DAILY_REQUEST,
  STUDENTS_REPORTS_DAILY_SUCCESS,
  STUDENTS_REPORTS_DAILY_FAIL,
} from "../constants/studentConstants"
import PORT from "../utils/baseURL"

export const listStudents =
  (keyword = "", pageNumber = "", userId = "") =>
  async (dispatch, getState) => {
    try {
      dispatch({ type: GET_STUDENTS_REQUEST })

      const {
        userLogin: { userInfo },
      } = getState()

      const { data } = await axios.get(
        `${PORT}/api/students?keyword=${keyword}&pageNumber=${pageNumber}&userId=${userId}`,
        {
          headers: { Authorization: `Bearer ${userInfo.token}` },
        }
      )

      dispatch({ type: GET_STUDENTS_SUCCESS, payload: data })
    } catch (error) {
      dispatch({
        type: GET_STUDENTS_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }

export const listStudent = studentId => async (dispatch, getState) => {
  try {
    dispatch({ type: STUDENT_DETAILS_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    const { data } = await axios.get(`${PORT}/api/students/${studentId}`, {
      headers: {
        Authorization: `Bearer ${userInfo.token}`,
      },
    })

    dispatch({ type: STUDENT_DETAILS_SUCCESS, payload: data })
  } catch (error) {
    dispatch({
      type: STUDENT_DETAILS_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

export const deleteStudent = studentId => async (dispatch, getState) => {
  try {
    dispatch({ type: DELETE_STUDENT_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    await axios.delete(`${PORT}/api/students/${studentId}`, {
      headers: {
        Authorization: `Bearer ${userInfo.token}`,
      },
    })

    dispatch({ type: DELETE_STUDENT_SUCCESS })
  } catch (error) {
    dispatch({
      type: DELETE_STUDENT_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

export const updateStudent =
  (studentId, studentData) => async (dispatch, getState) => {
    try {
      dispatch({ type: UPDATE_STUDENT_REQUEST })

      const {
        userLogin: { userInfo },
      } = getState()

      const { data } = await axios.put(
        `${PORT}/api/students/${studentId}`,
        studentData,
        { headers: { Authorization: `Bearer ${userInfo.token}` } }
      )

      dispatch({ type: UPDATE_STUDENT_SUCCESS, payload: data })
    } catch (error) {
      dispatch({
        type: UPDATE_STUDENT_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }

export const addStudent = student => async (dispatch, getState) => {
  try {
    dispatch({ type: ADD_STUDENT_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    const { data } = await axios.post(`${PORT}/api/students`, student, {
      headers: { Authorization: `Bearer ${userInfo.token}` },
    })

    dispatch({ type: ADD_STUDENT_SUCCESS, payload: data })
  } catch (error) {
    dispatch({
      type: ADD_STUDENT_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

