import React, { useEffect } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import { Button, Col, Container, ListGroup, Row } from "react-bootstrap"
import {
  deleteSeriesLevelById,
  getSeriesDetails,
  getSeriesLevelDetails,
} from "../actions/seriesAction"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { Link } from "react-router-dom"
import { DELETE_SERIES_LEVEL_RESET } from "../constants/seriesConstants"

const SeriesLevel = () => {
  const { seriesId, levelId } = useParams()

  const dispatch = useDispatch()

  const seriesLevelDetails = useSelector(state => state.seriesLevelDetails)
  const { loading, error, level } = seriesLevelDetails

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { series } = seriesDetails

  const seriesLevelDelete = useSelector(state => state.seriesLevelDelete)
  const {
    loading: seriesLevelLoading,
    error: seriesLevelError,
    success: seriesLevelSuccess,
  } = seriesLevelDelete

  const navigate = useNavigate()

  useEffect(() => {
    if (!series) {
      dispatch(getSeriesDetails(seriesId))
    }
    dispatch(getSeriesLevelDetails(seriesId, levelId))

    if (seriesLevelSuccess) {
      navigate("/serieses")
      dispatch({ type: DELETE_SERIES_LEVEL_RESET })
    }
  }, [dispatch, seriesId, series, levelId, seriesLevelSuccess, navigate])

  const handleSeriesLevelDelete = () => {
    if (window.confirm("Are You Sure ?")) {
      dispatch(deleteSeriesLevelById(seriesId, levelId))
    }
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : seriesLevelLoading ? (
    <Loader />
  ) : seriesLevelError ? (
    <Message variant="danger">{seriesLevelError}</Message>
  ) : (
    level && (
      <Container>
        <h3 className="my-4">
          {series && series.name} {level && level.levelName}
        </h3>
        <Row>
          <Col md={8}>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <p>
                  <strong>Level Name : </strong>
                  {level.levelName}
                </p>
              </ListGroup.Item>
              <ListGroup.Item>
                <p>
                  <strong>Level Time : </strong>
                  {level.levelTime}
                </p>
              </ListGroup.Item>
              <ListGroup.Item>
                <p>
                  <strong>Level Fess : </strong>
                  {level.levelFees}
                </p>
              </ListGroup.Item>
              <ListGroup.Item>
                <p>
                  <strong>Level Salary : </strong>
                  {level.levelSalary}
                </p>
              </ListGroup.Item>
              <ListGroup.Item>
                <p>
                  <strong>Level Students Count : </strong>
                  {level.levelStudents && level.levelStudents.length}
                </p>
              </ListGroup.Item>
              <ListGroup.Item>
                <p>
                  <strong>Level Teachers Count : </strong>
                  {level.levelTeachers && level.levelTeachers.length}
                </p>
              </ListGroup.Item>

              <ListGroup.Item>
                <Link
                  className="mx-1 btn btn-info btn-sm"
                  to={`/serieses/${seriesId}/levels/${levelId}/students`}
                >
                  Students List
                </Link>
                <Link
                  className="mx-1 btn btn-info btn-sm"
                  to={`/serieses/${seriesId}/levels/${levelId}/teachers`}
                >
                  Teachers List
                </Link>
              </ListGroup.Item>
            </ListGroup>
          </Col>
          <Col md={4}>
            <ListGroup>
              <ListGroup.Item>
                <strong>Actions</strong>
              </ListGroup.Item>
              <ListGroup.Item>
                <Link
                  to={`/serieses/${seriesId}/levels/${levelId}/add/teacher`}
                  className="btn btn-success btn-sm"
                >
                  Add New Teacher
                </Link>
              </ListGroup.Item>
              <ListGroup.Item>
                <Link
                  className="btn btn-success btn-sm"
                  to={`/serieses/${seriesId}/levels/${levelId}/add/student`}
                >
                  Add New Student
                </Link>
              </ListGroup.Item>
              <ListGroup.Item>
                <Button
                  variant="danger"
                  size="sm"
                  type="button"
                  onClick={handleSeriesLevelDelete}
                >
                  Delete Level
                </Button>
              </ListGroup.Item>
              <ListGroup.Item>
                <Link
                  to={`/serieses/${seriesId}/levels/${levelId}/edit`}
                  className="btn btn-warning btn-sm"
                >
                  Update Level
                </Link>
              </ListGroup.Item>
            </ListGroup>
          </Col>
        </Row>
      </Container>
    )
  )
}

export default SeriesLevel
