import React, { useEffect, useState } from "react"
import { Button, Container, Form } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"

import { updateSeriesLevelById } from "../actions/seriesAction"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { UPDATE_SERIES_LEVEL_RESET } from "../constants/seriesConstants"

const SeriesLevelEditScreen = () => {
  const [levelName, setLevelName] = useState("")
  const [levelTime, setLevelTime] = useState("")
  const [levelFees, setLevelFees] = useState(0)
  const [levelBookFees, setLevelBookFees] = useState(0)
  const [levelSalary, setLevelSalary] = useState(0)

  const { seriesId, levelId } = useParams()

  const navigate = useNavigate()

  const dispatch = useDispatch()

  const seriesLevelDetails = useSelector(state => state.seriesLevelDetails)
  const { level, loading, error } = seriesLevelDetails

  const seriesLevelUpdate = useSelector(state => state.seriesLevelUpdate)
  const {
    success,
    loading: updateLoading,
    error: updateError,
  } = seriesLevelUpdate

  useEffect(() => {
    if (success) {
      navigate("/serieses")
      dispatch({ type: UPDATE_SERIES_LEVEL_RESET })
    }

    if (level && level.levelName) {
      setLevelName(level.levelName)
      setLevelTime(level.levelTime)
      setLevelFees(level.levelFees)
      setLevelBookFees(level.levelBookFees)
      setLevelSalary(level.levelSalary)
    }
  }, [navigate, dispatch, success, level])

  const handleSubmit = e => {
    e.preventDefault()

    dispatch(
      updateSeriesLevelById(seriesId, levelId, {
        levelName,
        levelTime,
        levelFees,
        levelBookFees,
        levelSalary,
      })
    )
  }

  console.log(typeof levelBookFees)

  return loading ? (
    <Loader />
  ) : updateLoading ? (
    <Loader />
  ) : (
    <Container>
      <h3 className="my-4">Edit Series Level</h3>
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="levelName" className="my-3">
          <Form.Label>Level Name</Form.Label>
          <Form.Control
            type="text"
            required
            value={levelName}
            onChange={e => setLevelName(e.target.value)}
            placeholder="Enter Level Name"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelTime" className="my-3">
          <Form.Label>Level Time</Form.Label>
          <Form.Control
            type="text"
            required
            value={levelTime}
            onChange={e => setLevelTime(e.target.value)}
            placeholder="Enter Level Time"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelFees" className="my-3">
          <Form.Label>Level Fees</Form.Label>
          <Form.Control
            type="number"
            required
            value={levelFees}
            onChange={e => setLevelFees(e.target.value)}
            placeholder="Enter Level Fees"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelBookFees" className="my-3">
          <Form.Label>Level Book Fees</Form.Label>
          <Form.Control
            type="number"
            required
            value={levelBookFees}
            onChange={e => setLevelBookFees(e.target.value)}
            placeholder="Enter Level Book Fees"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="levelSalary" className="my-3">
          <Form.Label>Level Salary</Form.Label>
          <Form.Control
            type="number"
            required
            value={levelSalary}
            onChange={e => setLevelSalary(e.target.value)}
            placeholder="Enter Level Salary"
          ></Form.Control>
        </Form.Group>

        {error && <Message variant="danger">{error}</Message>}
        {updateError && <Message variant="danger">{updateError}</Message>}

        <Button type="submit" variant="success" className="mt-4">
          Update
        </Button>
      </Form>
    </Container>
  )
}

export default SeriesLevelEditScreen
