import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Form, Button, Row, Col, Container } from "react-bootstrap"
import { Link, useNavigate, useParams } from "react-router-dom"

import FormContainer from "../components/FormContainer"
import Message from "../components/Message"
import Loader from "../components/Loader"
import { UPDATE_TEACHER_RESET } from "../constants/teachersConstants"
import { listTeacher, updateTeacher } from "../actions/teacherAction"

const TeacherEditScreen = () => {
  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [phoneNumber, setPhoneNumber] = useState("")
  const [salary, setSalary] = useState("")
  const [attendanceTime, setAttendanceTime] = useState("")
  const [teachingField, setTeachingField] = useState("")

  const dispatch = useDispatch()

  const navigate = useNavigate()

  const { teacherId } = useParams()

  const teacherDetails = useSelector(state => state.teacherDetails)
  const {
    loading: detailsLoading,
    error: detailsError,
    teacher,
  } = teacherDetails

  const teacherUpdate = useSelector(state => state.teacherUpdate)
  const { loading, error, success } = teacherUpdate

  useEffect(() => {
    if (!teacher) {
      dispatch(listTeacher(teacherId))
    } else {
      if (teacher.name) {
        setName(teacher.name)
        setEmail(teacher.email)
        setAttendanceTime(teacher.attendanceTime)
        setTeachingField(teacher.teachingField)
        setSalary(teacher.salary)
        setPhoneNumber(teacher.phoneNumber)
      }
    }
    if (success) {
      dispatch({ type: UPDATE_TEACHER_RESET })
      navigate("/teachers")
    }
  }, [dispatch, navigate, teacherId, teacher, success])

  const handleSubmit = e => {
    e.preventDefault()

    dispatch(
      updateTeacher(teacherId, {
        name,
        email,
        phoneNumber,
        attendanceTime,
        teachingField,
        salary,
      })
    )
  }

  return detailsLoading ? (
    <Loader />
  ) : detailsError ? (
    <Message variant="danger">{detailsError}</Message>
  ) : loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <Container fluid className="mt-5">
      <FormContainer>
        <Row>
          <Col md={5}>
            <Link to="/teachers" className="btn btn-sm btn-outline-secondary">
              Go Back
            </Link>
          </Col>
          <Col></Col>
        </Row>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md={6}>
              <Form.Group controlId="name" className="m-2">
                <Form.Label> Name </Form.Label>
                <Form.Control
                  type="text"
                  value={name}
                  placeholder="Enter Name"
                  onChange={e => setName(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group controlId="email" className="m-2">
                <Form.Label> Email </Form.Label>
                <Form.Control
                  type="email"
                  value={email}
                  placeholder="Enter Father Name"
                  onChange={e => setEmail(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <Form.Group controlId="phoneNumber" className="m-2">
                <Form.Label>Phone Number</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Phone Number"
                  value={phoneNumber}
                  onChange={e => setPhoneNumber(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group controlId="attendanceTime" className="m-2 ">
                <Form.Label>Attendance Time</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Attendance Time"
                  value={attendanceTime}
                  onChange={e => setAttendanceTime(e.target.value)}
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <Form.Group controlId="salary" className="m-2">
                <Form.Label> Salary </Form.Label>
                <Form.Control
                  type="number"
                  value={salary}
                  placeholder="Salary"
                  onChange={e => setSalary(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group controlId="teachingField" className="m-2">
                <Form.Label>Teaching Field</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Teaching Field"
                  value={teachingField}
                  onChange={e => setTeachingField(e.target.value)}
                ></Form.Control>
              </Form.Group>
            </Col>
          </Row>
          <Button type="submit" variant="success" className="my-4">
            Update Teacher
          </Button>
        </Form>
      </FormContainer>
    </Container>
  )
}

export default TeacherEditScreen
