import React, { useEffect } from "react"
import { Col, Container, Row } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"

import Loader from "../components/Loader"
import Message from "../components/Message"
import { listSeries } from "../actions/seriesAction"
import SeriesDetails from "../components/SeriesDetails"

const SeriesScreen = () => {
  const seriesList = useSelector(state => state.seriesList)
  const { loading, error, serieses } = seriesList

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(listSeries())
  }, [dispatch])

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <Container>
      <h2 className="mb-5 mt-2 text-center">Serieses</h2>
      <Row>
        {serieses &&
          serieses.length > 0 &&
          serieses.map(series => (
            <Col key={series._id} md={4}>
              <SeriesDetails
                seriesId={series._id}
                name={series.name}
                seriesStartDate={series.seriesStartDate}
                seriesEndDate={series.seriesEndDate}
                dailyStudyTime={series.dailyStudyTime}
              />
            </Col>
          ))}
      </Row>
    </Container>
  )
}

export default SeriesScreen
