import React from 'react'
import { Spinner } from 'react-bootstrap'

const Loader = () => {
  return (
    <div className="loader">
      <Spinner
        animation="border"
        role="status"
        style={{ color: '#3498db', width: '100%', height: '100%' }}
      />
    </div>
  )
}

export default Loader
