export const listSeries = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_SERIESES_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    const { data } = await axios.get(`${PORT}/api/series`, {
      headers: {
        Authorization: `Bearer ${userInfo.token}`,
      },
    })

    dispatch({ type: GET_SERIESES_SUCCESS, payload: data })
  } catch (error) {
    dispatch({
      type: GET_SERIESES_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

