import React, { useEffect, useRef, useState } from "react"
import { Button, Container, Form, Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useParams } from "react-router-dom"
import {
  feesPaidToLevelStudent,
  getSeriesLevelDetails,
} from "../actions/seriesAction"
import Loader from "../components/Loader"
import Message from "../components/Message"

const LevelStudentsListScreen = () => {
  const { levelId, seriesId } = useParams()

  const [currentStudent, setCurrentStudent] = useState({})
  const [isFessPaidSuccess, setIsFessPaidSuccess] = useState(false)

  const dispatch = useDispatch()

  const seriesDetails = useSelector(state => state.seriesDetails)
  const { series } = seriesDetails

  const seriesLevelDetails = useSelector(state => state.seriesLevelDetails)
  const { loading, error, level } = seriesLevelDetails

  const { studentId, isChecked } = currentStudent
  useEffect(() => {
    dispatch(getSeriesLevelDetails(seriesId, levelId))

    if (isFessPaidSuccess) {
      dispatch(getSeriesLevelDetails(seriesId, levelId))
    }
  }, [dispatch, seriesId, levelId, isFessPaidSuccess, studentId, isChecked])

  const findFeesPaid = stu => {
    return (
      level &&
      stu.fesses.find(fee => {
        const fees = fee.level.toString() === levelId.toString() ? fee : {}
        return fees.isPaid
      })
    )
  }

  const formSubmitRef = useRef()

  const handleCheckChange = (e, studentId) => {
    setCurrentStudent({
      studentId,
      isChecked: e.target.checked,
    })

    formSubmitRef.current.click()
  }

  const handleFessSubmission = event => {
    event.preventDefault()

    setIsFessPaidSuccess(prevValue => !prevValue)

    if (currentStudent && currentStudent.studentId) {
      dispatch(
        feesPaidToLevelStudent(seriesId, levelId, currentStudent.studentId, {
          isPaid: currentStudent.isChecked,
        })
      )
    }
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    level && (
      <Container>
        <h3 className="mb-4 mt-1">
          {series && series.name} Series {level && level.levelName} Students
          List
        </h3>
        <Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>NAME</th>
              <th>F/NAME</th>
              <th>GENDER</th>
              <th>PHONE NUMBER</th>
              <th>REGISTERED AT</th>
              <th>LEVEL FEES</th>
              <th>IS PAID</th>
            </tr>
          </thead>
          <tbody>
            {level &&
              level.levelStudents &&
              level.levelStudents.length > 0 &&
              level.levelStudents.map(stu => (
                <tr key={stu._id}>
                  <td>{stu.name}</td>
                  <td>{stu.fatherName}</td>
                  <td>{stu.gender}</td>
                  <td>{stu.phoneNumber}</td>
                  <td>{stu.createdAt.substring(0, 10)}</td>
                  <td>{level.levelFees}</td>
                  <td>
                    <>
                      {findFeesPaid(level && stu) ? (
                        <i
                          className="fa fa-check d-inline"
                          style={{ color: "green" }}
                        ></i>
                      ) : (
                        <i className="fa fa-times" style={{ color: "red" }}></i>
                      )}
                      <Form
                        className="d-inline"
                        onSubmit={handleFessSubmission}
                      >
                        <Form.Check
                          checked={findFeesPaid(level && stu)}
                          className="d-inline-block mx-3"
                          onChange={e => handleCheckChange(e, stu._id)}
                        ></Form.Check>
                        <Button
                          ref={formSubmitRef}
                          style={{ display: "none" }}
                          type="submit"
                        ></Button>
                      </Form>
                    </>
                  </td>
                </tr>
              ))}
          </tbody>
        </Table>
      </Container>
    )
  )
}

export default LevelStudentsListScreen
