export const seriesListReducer = (state = { serieses: [] }, action) => {
  switch (action.type) {
    case GET_SERIESES_REQUEST:
      return { loading: true }
    case GET_SERIESES_SUCCESS:
      return {
        loading: false,
        serieses: action.payload,
      }
    case GET_SERIESES_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

export const seriesDetailsReducer = (state = {}, action) => {
  switch (action.type) {
    case GET_SERIES_DETAILS_REQUEST:
      return { loading: true }
    case GET_SERIES_DETAILS_SUCCESS:
      return {
        loading: false,
        series: action.payload[0],
      }
    case GET_SERIES_DETAILS_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

