export const seriesDeleteByIdReducer = (state = {}, action) => {
  switch (action.key) {
    case DELETE_SERIES_BY_ID_REQUEST:
      return {
        loading: true,
      }
    case DELETE_SERIES_BY_ID_SUCCESS:
      return {
        loading: false,
        success: true,
      }
    case DELETE_SERIES_BY_ID_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    case DELETE_SERIES_BY_ID_RESET:
      return {}
    default:
      return state
  }
}

