import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Form, Button, Row, Col, Container } from "react-bootstrap"
import { Link, useNavigate } from "react-router-dom"

import FormContainer from "../components/FormContainer"
import { addStudent } from "../actions/studentsAction"
import Message from "../components/Message"
import Loader from "../components/Loader"
import { ADD_STUDENT_RESET } from "../constants/studentConstants"

const AddStudentScreen = () => {
  const [name, setName] = useState("")
  const [fatherName, setFatherName] = useState("")
  const [gender, setGender] = useState("male")
  const [phoneNumber, setStudentPhoneNumber] = useState("")
  const [address, setAddress] = useState("")
  const [registrationFees, setRegistrationFees] = useState({
    feesAmount: 0,
    isPaid: false,
  })
  const [cardFees, setCardFees] = useState({
    feesAmount: 0,
    isPaid: false,
  })

  const dispatch = useDispatch()

  const navigate = useNavigate()

  const studentAdd = useSelector(state => state.studentAdd)
  const { loading, error, success } = studentAdd

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  useEffect(() => {
    if (!userInfo) {
      navigate("/login?redirect=add/student")
    }
    if (success) {
      dispatch({ type: ADD_STUDENT_RESET })
      navigate("/")
    }
  }, [dispatch, success, navigate, userInfo])

  const registrationFeesChangeHandler = event => {
    if (event.target.name === "feesAmount") {
      setRegistrationFees(prevValues => {
        return {
          feesAmount: event.target.value,
          isPaid: prevValues.isPaid,
        }
      })
    } else if (event.target.name === "isPaid") {
      setRegistrationFees(prevValues => {
        return {
          feesAmount: prevValues.feesAmount,
          isPaid: event.target.checked,
        }
      })
    }
  }

  const cardFeesChangeHandler = event => {
    if (event.target.name === "feesAmount") {
      setCardFees(prevValues => {
        return {
          feesAmount: event.target.value,
          isPaid: prevValues.isPaid,
        }
      })
    } else if (event.target.name === "isPaid") {
      setCardFees(prevValues => {
        return {
          feesAmount: prevValues.feesAmount,
          isPaid: event.target.checked,
        }
      })
    }
  }

  const handleSubmit = e => {
    e.preventDefault()

    dispatch(
      addStudent({
        name,
        fatherName,
        gender,
        phoneNumber,
        address,
        registrationFees,
        cardFees,
      })
    )
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <Container fluid className="mt-5 px-4">
      <Row>
        <Col md={5}>
          <Link to="/" className="btn btn-sm btn-outline-secondary">
            Go Back
          </Link>
        </Col>
        <Col></Col>
      </Row>
      <Form onSubmit={handleSubmit} className="mt-3">
        <Row>
          <Col md={6}>
            <Form.Group controlId="name" className="m-2">
              <Form.Label> Name </Form.Label>
              <Form.Control
                type="text"
                value={name}
                placeholder="Enter Name"
                onChange={e => setName(e.target.value)}
              ></Form.Control>
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group controlId="fatherName" className="m-2">
              <Form.Label> Father Name </Form.Label>
              <Form.Control
                type="text"
                value={fatherName}
                placeholder="Enter Father Name"
                onChange={e => setFatherName(e.target.value)}
              ></Form.Control>
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Form.Group className="m-2">
              <Form.Label htmlFor="gender">Select Gender</Form.Label>
              <Form.Select
                id="gender"
                value={gender}
                onChange={e => setGender(e.target.value)}
              >
                <option value="male">Male</option>
                <option value="female">Female</option>
              </Form.Select>
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group controlId="address" className="m-2">
              <Form.Label> Address </Form.Label>
              <Form.Control
                type="text"
                value={address}
                placeholder="Enter Address"
                onChange={e => setAddress(e.target.value)}
              ></Form.Control>
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Form.Group controlId="phoneNumber" className="m-2">
              <Form.Label> Student Phone Number </Form.Label>
              <Form.Control
                type="text"
                value={phoneNumber}
                placeholder="Enter Student Phone Number"
                onChange={e => setStudentPhoneNumber(e.target.value)}
              ></Form.Control>
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group controlId="registrationFees">
              <Form.Label>Registration Fees Details</Form.Label>
              <Row>
                <Col md={6}>
                  <Form.Label>Fees Amount</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Registration Fees Amount"
                    name="feesAmount"
                    value={registrationFees.feesAmount}
                    onChange={registrationFeesChangeHandler}
                  ></Form.Control>
                </Col>
                <Col md={6}>
                  <Form.Label>is Paid</Form.Label>
                  <Form.Check
                    value={registrationFees.isPaid}
                    checked={registrationFees.isPaid}
                    name="isPaid"
                    onChange={registrationFeesChangeHandler}
                  ></Form.Check>
                </Col>
              </Row>
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Form.Group controlId="cardFees">
              <Form.Label>Card Fees Details</Form.Label>
              <Row>
                <Col md={6}>
                  <Form.Label>Fees Amount</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Card Fees Amount"
                    name="feesAmount"
                    value={cardFees.feesAmount}
                    onChange={cardFeesChangeHandler}
                  ></Form.Control>
                </Col>
                <Col md={6}>
                  <Form.Label>is Paid</Form.Label>
                  <Form.Check
                    value={cardFees.isPaid}
                    checked={cardFees.isPaid}
                    name="isPaid"
                    onChange={cardFeesChangeHandler}
                  ></Form.Check>
                </Col>
              </Row>
            </Form.Group>
          </Col>
        </Row>

        <Button type="submit" variant="success" className="my-4">
          Add Student
        </Button>
      </Form>
    </Container>
  )
}

export default AddStudentScreen
