import {
  legacy_createStore,
  combineReducers,
  compose,
  applyMiddleware,
} from "redux"
import thunk from "redux-thunk"
import { composeWithDevTools } from "redux-devtools-extension"

import {
  studentAddReducer,
  studentDeleteReducer,
  studentDetailsReducer,
  studentListReducer,
  studentsDailyReportsReducer,
  studentsMonthlyReportsReducer,
  studentsWeeklyReportsReducer,
  studentsYearlyReportsReducer,
  studentUpdateReducer,
} from "./reducers/studentsReducers"
import {
  teacherAddReducer,
  teacherDeleteReducer,
  teacherDetailsReducer,
  teacherListReducer,
  teachersReportReducer,
  teacherUpdateReducer,
} from "./reducers/teachersReducers"
import {
  adminUserDeleteReducer,
  adminUserDetailsReducer,
  adminUserUpdateReducer,
  userDeleteReducer,
  userDetailsReducer,
  userListReducer,
  userLoginReducer,
  userSignupReducer,
  userUpdateReducer,
} from "./reducers/usersReducers"
import {
  seriesAddReducer,
  seriesDeleteByIdReducer,
  seriesDetailsReducer,
  seriesLevelAddReducer,
  seriesLevelDeleteReducer,
  seriesLevelDetailsReducer,
  seriesLevelStudentAddReducer,
  seriesLevelStudentDeleteReducer,
  seriesLevelStudentFessReducer,
  seriesLevelTeacherAddReducer,
  seriesLevelTeacherDeleteReducer,
  seriesLevelTeacherSalaryReducer,
  seriesLevelUpdateReducer,
  seriesListReducer,
  seriesUpdateByIdReducer,
} from "./reducers/seriesReducers"

const reducers = combineReducers({
  studentList: studentListReducer,
  studentDetails: studentDetailsReducer,
  studentDelete: studentDeleteReducer,
  studentUpdate: studentUpdateReducer,
  studentAdd: studentAddReducer,
  studentsYearlyReports: studentsYearlyReportsReducer,
  studentsMonthlyReports: studentsMonthlyReportsReducer,
  studentsWeeklyReports: studentsWeeklyReportsReducer,
  studentsDailyReports: studentsDailyReportsReducer,

  teacherList: teacherListReducer,
  teacherDetails: teacherDetailsReducer,
  teacherDelete: teacherDeleteReducer,
  teacherUpdate: teacherUpdateReducer,
  teacherAdd: teacherAddReducer,
  teachersReport: teachersReportReducer,

  userSignup: userSignupReducer,
  userLogin: userLoginReducer,
  userDetails: userDetailsReducer,
  userUpdate: userUpdateReducer,
  userDelete: userDeleteReducer,
  userList: userListReducer,
  adminUserDetails: adminUserDetailsReducer,
  adminUserDelete: adminUserDeleteReducer,
  adminUserUpdate: adminUserUpdateReducer,

  seriesAdd: seriesAddReducer,
  seriesList: seriesListReducer,
  seriesDetails: seriesDetailsReducer,
  seriesUpdateById: seriesUpdateByIdReducer,
  seriesDeleteById: seriesDeleteByIdReducer,
  seriesLevelDetails: seriesLevelDetailsReducer,
  seriesLevelAdd: seriesLevelAddReducer,
  seriesLevelUpdate: seriesLevelUpdateReducer,
  seriesLevelDelete: seriesLevelDeleteReducer,
  seriesLevelStudentAdd: seriesLevelStudentAddReducer,
  seriesLevelStudentFees: seriesLevelStudentFessReducer,
  seriesLevelStudentDelete: seriesLevelStudentDeleteReducer,
  seriesLevelTeacherAdd: seriesLevelTeacherAddReducer,
  seriesLevelTeacherSalary: seriesLevelTeacherSalaryReducer,
  seriesLevelTeacherDelete: seriesLevelTeacherDeleteReducer,
})

const userInfoFromStorage = localStorage.getItem("userInfo")
  ? JSON.parse(localStorage.getItem("userInfo"))
  : null

const initialState = {
  userLogin: { userInfo: userInfoFromStorage },
}

const middleWares = [thunk]

const composeEnhancer = composeWithDevTools || compose

const store = legacy_createStore(
  reducers,
  initialState,
  composeEnhancer(applyMiddleware(...middleWares))
)

export default store
