import React, { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Table, Button, Container, Row, Col } from "react-bootstrap"
import { useNavigate } from "react-router-dom"

import { deleteTeacher, listTeachers } from "../actions/teacherAction"
import Loader from "../components/Loader"
import Message from "../components/Message"

const TeachersScreen = () => {
  const dispatch = useDispatch()

  const navigate = useNavigate()

  const teacherList = useSelector(state => state.teacherList)
  const { loading, error, teachers } = teacherList

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  const teacherDelete = useSelector(state => state.teacherDelete)
  const { loading: deleteLoading, error: deleteError, success } = teacherDelete

  useEffect(() => {
    if (!userInfo) {
      navigate("/login?redirect=teachers")
    }
    dispatch(listTeachers())
    if (success) {
      dispatch(listTeachers())
    }
  }, [dispatch, success, navigate, userInfo])

  const handleTeacherDelete = teacherId => {
    if (window.confirm("Are you sure ?")) {
      dispatch(deleteTeacher(teacherId))
    }
  }
  const handleTeacherUpdate = teacherId => {
    navigate(`/teachers/${teacherId}/edit`)
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : deleteError ? (
    <Message variant="danger">{deleteError}</Message>
  ) : deleteLoading ? (
    <Loader />
  ) : (
    <Container>
      <Row>
        <Col md={10}>
          <h3 className="mb-3">Teachers</h3>
        </Col>
        <Col md={2}>
          <Button
            type="button"
            size="sm"
            variant="info"
            onClick={() => window.print()}
          >
            Print
          </Button>
        </Col>
      </Row>
      <Table
        striped
        bordered
        hover
        responsive
        size="sm"
        className="print-container"
      >
        <thead>
          <tr>
            <th>Id</th>
            <th>Name</th>
            <th>Email</th>
            <th>Phone Number</th>
            <th>Registered At</th>
            <th className="avoid-print"></th>
          </tr>
        </thead>

        <tbody>
          {teachers &&
            teachers.length > 0 &&
            teachers.map(teacher => (
              <tr key={teacher._id}>
                <td>{teacher._id.substring(20, 25)}</td>
                <td>{teacher.name}</td>
                <td>{teacher.email}</td>
                <td>{teacher.phoneNumber}</td>
                <td>{teacher.createdAt.substring(0, 10)}</td>

                <td className="avoid-print">
                  <Button
                    type="button"
                    variant="warning"
                    className="mx-2 btn-sm"
                    onClick={() => handleTeacherUpdate(teacher._id)}
                  >
                    <i className="fa fa-edit"></i>
                  </Button>

                  <Button
                    type="button"
                    variant="danger"
                    className="mx-2 btn-sm"
                    onClick={() => handleTeacherDelete(teacher._id)}
                  >
                    <i className="fa fa-trash"></i>
                  </Button>
                </td>
              </tr>
            ))}
        </tbody>
      </Table>
    </Container>
  )
}

export default TeachersScreen
