import axios from "axios"

import {
  GET_TEACHERS_REQUEST,
  GET_TEACHERS_SUCCESS,
  GET_TEACHERS_FAIL,
  DELETE_TEACHER_REQUEST,
  DELETE_TEACHER_SUCCESS,
  DELETE_TEACHER_FAIL,
  UPDATE_TEACHER_REQUEST,
  UPDATE_TEACHER_SUCCESS,
  UPDATE_TEACHER_FAIL,
  TEACHER_DETAILS_REQUEST,
  TEACHER_DETAILS_SUCCESS,
  TEACHER_DETAILS_FAIL,
  ADD_TEACHER_REQUEST,
  ADD_TEACHER_SUCCESS,
  ADD_TEACHER_FAIL,
  GET_TEACHERS_REPORTS_FAIL,
  GET_TEACHERS_REPORTS_REQUEST,
  GET_TEACHERS_REPORTS_SUCCESS,
} from "../constants/teachersConstants"
import PORT from "../utils/baseURL"

export const listTeachers = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_TEACHERS_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    const { data } = await axios.get(`${PORT}/api/teachers`, {
      headers: { Authorization: `Bearer ${userInfo.token}` },
    })

    dispatch({ type: GET_TEACHERS_SUCCESS, payload: data })
  } catch (error) {
    dispatch({
      type: GET_TEACHERS_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

export const listTeacher = teacherId => async (dispatch, getState) => {
  try {
    dispatch({ type: TEACHER_DETAILS_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    const { data } = await axios.get(`${PORT}/api/teachers/${teacherId}`, {
      headers: { Authorization: `Bearer ${userInfo.token}` },
    })

    dispatch({ type: TEACHER_DETAILS_SUCCESS, payload: data })
  } catch (error) {
    dispatch({
      type: TEACHER_DETAILS_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

export const deleteTeacher = teacherId => async (dispatch, getState) => {
  try {
    dispatch({ type: DELETE_TEACHER_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    await axios.delete(`${PORT}/api/teachers/${teacherId}`, {
      headers: { Authorization: `Bearer ${userInfo.token}` },
    })

    dispatch({ type: DELETE_TEACHER_SUCCESS })
  } catch (error) {
    dispatch({
      type: DELETE_TEACHER_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

export const updateTeacher =
  (teacherId, teacherData) => async (dispatch, getState) => {
    try {
      dispatch({ type: UPDATE_TEACHER_REQUEST })

      const {
        userLogin: { userInfo },
      } = getState()

      const { data } = await axios.put(
        `${PORT}/api/teachers/${teacherId}`,
        teacherData,
        { headers: { Authorization: `Bearer ${userInfo.token}` } }
      )

      dispatch({ type: UPDATE_TEACHER_SUCCESS, payload: data })
    } catch (error) {
      dispatch({
        type: UPDATE_TEACHER_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }

export const addTeacher = teacher => async (dispatch, getState) => {
  try {
    dispatch({ type: ADD_TEACHER_REQUEST })

    const {
      userLogin: { userInfo },
    } = getState()

    const { data } = await axios.post(`${PORT}/api/teachers`, teacher, {
      headers: { Authorization: `Bearer ${userInfo.token}` },
    })

    dispatch({ type: ADD_TEACHER_SUCCESS, payload: data })
  } catch (error) {
    dispatch({
      type: ADD_TEACHER_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

